@import url(https://fonts.googleapis.com/css2?family=Commissioner&family=Cuprum&display=swap);
header {
  width: 100%;
}

.navbar {
  justify-content: space-between;
  width: 100%;
  max-width: 1400px;
  padding: 25px 0;
  background: #fff;
}

.nav-logo {
  margin-left: 50px;
  width: 50px;
}

.nav-links {
  font-size: 1.1rem;
  list-style: none;
  margin: 0 10px;
}

.link {
  text-decoration: none;
  margin: 0 20px;
  width: 150px;
}

.link-black {
  color: #000;
  border: 1px solid #d6d6d6;
}

.link-brown {
  background: #db9768;
  color: #fff;
  border: 1px solid #ca7d45;
}

.link-black:hover {
  background: #000;
  color: #fff;
}

.link-brown:hover {
  background: #596c5a;
  border: 1px solid #485849;
}

#cart-link {
  width: 50px;
  margin-right: 30px;
}

.fa-shopping-cart {
  color: #adadad;
}

.fa-shopping-cart:hover {
  color: #000;
  transform: scale(1.2);
}

/* --------------------------- */
/* ------ SMALL DISPLAY ------ */
/* --------------------------- */

.menu-icon {
  margin-right: 50px;
}

.menu-icon > i {
  cursor: pointer;
}

.nav-menu {
  position: fixed;
  right: -100%;
  top: 0;
  width: 50%;
  height: 100vh;
  background: #596c5a;
  z-index: 100;
  transition: 0.5s;
  box-shadow: -5px 0 10px -5px #111111;
}

.active {
  right: 0;
  transition: 0.5s;
}

.nav-menu ul {
  list-style: none;
  font-size: 2rem;
  margin-top: 4rem;
  width: 100%;
}

#nav-menu-toggle {
  display: block;
  cursor: pointer;
  position: absolute;
  right: 25px;
  top: 25px;
  color: #fff;
}

.nav-menu-link {
  text-decoration: none;
  border: 2px solid #fff;
  padding: 10px;
  margin: 25px;
}

.nav-menu-link:hover {
  background: rgba(255, 255, 255, 0.25);
}

.nav-menu-link p {
  color: #fff;
  text-align: center;
}

footer {
  width: 100%;
  background: #596c5a;
}

.footer-container {
  width: 100%;
  max-width: 1400px;
  padding: 0 50px;
  color: white;
  background: #596c5a;
}

.footer-logo {
  box-sizing: unset;
  padding: 50px 0;
  height: 75px;
}

.footer-content {
  flex: 1 1;
  padding: 20px;
}

.footer-content > a {
  color: #fff;
}

.footer-content-title {
  letter-spacing: 2px;
  padding-bottom: 20px;
  font-size: 1.25rem;
}

.footer-content-sub-title {
  padding-bottom: 10px;
  font-size: 0.9rem;
}

.footer-content-text {
  padding-bottom: 30px;
}

.border-top {
  border-top: 1px solid rgba(255, 255, 255, 0.1);
}

.border-right {
  border-right: 1px solid rgba(255, 255, 255, 0.1);
}

.copyright {
  color: rgba(255, 255, 255, 0.6);
  font-size: 0.9rem;
  padding: 10px 0 30px;
  margin: 0 20px;
}

.social-icon {
  width: 36px;
  height: 40px;
  margin-bottom: 20px;
}

#home {
  width: 100%;
  text-align: center;
}

#video-container {
  margin: 25px auto;
  height: 500px;
  max-width: 1400px;
}

#video-container video {
  width: 100%;
  height: 100%;
}

#home-text-container {
  box-sizing: unset;
  width: 100%;
  height: 150px;
  text-align: center;
  padding: 25px 0;
  color: #fff;
  background: #596c5a;
}

.home-text-m {
  max-width: 1400px;
  font-size: 2rem;
  margin: 0 125px;
  font-weight: bold;
  line-height: 1.6;
  letter-spacing: 1px;
}

.home-text-sm {
  font-size: 1.45rem;
  margin: 0 40px;
  font-weight: bold;
  line-height: 1.6;
  letter-spacing: 1px;
}

#gallery {
  margin: 100px;
  max-width: 1400px;
}

.gallery-item {
  height: 100%;
  margin: 10px;
}

.gallery-item > img {
  width: 100%;
  height: 100%;
}

#link-to-menu p {
  font-size: 2.5rem;
  font-weight: bold;
  letter-spacing: 3px;
  max-width: 1400px;
}

#link-to-menu div {
  padding: 20px;
}

#about {
  width: 100%;
  max-width: 1400px;
  height: 90vh;
  text-align: center;
  align-items: center;
  background-repeat: "no-repeat";
  background-position: center;
  background-size: cover;
  background-attachment: fixed;
}

#about-content {
  width: 80%;
  max-width: 1000px;
  background: rgba(255, 255, 255, 0.9);
}

.header-m {
  margin: 75px;
  font-size: 2.75rem;
  letter-spacing: 0.2em;
  text-shadow: white 0px 0px 1rem;
}

.text-m {
  margin: 0 75px 75px 75px;
  font-size: 1.5rem;
  font-weight: bold;
  line-height: 1.6;
  text-shadow: white 0px 0px 1rem;
}

.header-sm {
  margin: 40px;
  font-size: 2.25rem;
  letter-spacing: 0.2em;
}

.text-sm {
  margin: 0 40px 40px 40px;
  font-size: 1.2rem;
  font-weight: bold;
  line-height: 1.6;
}

.modal {
  position: fixed;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
  z-index: 10;
  background: rgba(0, 0, 0, 0.6);
}

.modal-content {
  position: relative;
  width: 375px;
  height: 575px;
  background: #fff;
}

#item-cover {
  height: 500px;
}

#item-cover > img {
  width: 100%;
  height: 100%;
}

#modal-actions {
  width: 100%;
  border-top: 1px solid #c1c1c1;
}

#quantity-form {
  width: 125px;
  margin: 15px 15px 0 15px;
  border: 1px solid #a1a1a1;
}

#quantity-form .btn {
  cursor: pointer;
  outline: none;
  width: 100%;
  font-size: 2rem;
  padding: 3px;
  text-align: center;
  border: none;
  background: #fff;
  color: #c1c1c1;
}

#quantity-form .btn:hover {
  color: #3f3f3f;
  background: #e4e4e4;
}

#quantity-display {
  width: 50px;
}

#quantity-display input {
  outline: none;
  border: none;
  width: 40px;
  height: 100%;
  font-size: 1.25rem;
  text-align: center;
}

input[type="number"]::-webkit-inner-spin-button,
input[type="number"]::-webkit-outer-spin-button {
  -webkit-appearance: none;
  margin: 0;
}

#add-to-cart {
  width: 100%;
  height: 45px;
  margin: 15px 15px 0 0;
}

#add-to-cart button {
  cursor: pointer;
  outline: none;
  width: 100%;
  height: 100%;
  border: none;
  font-size: 1.5rem;
  color: #fff;
  background: #7a9c7c;
}

#add-to-cart button:hover {
  background: #628564;
}

#close-btn {
  cursor: pointer;
  position: absolute;
  top: 0;
  right: 5px;
}

#close-btn .fa {
  color: #000000;
}

#close-btn .fa:hover {
  color: #c50000;
}

.menu-item {
  box-sizing: border-box;
  width: 225px;
  min-width: 110px;
  margin: 0 15px;
}

.menu-item img {
  width: 100%;
}

.menu-item img:hover {
  cursor: pointer;
  opacity: 0.6;
}

.menu-item-description {
  font-size: 1rem;
  text-align: center;
}

.menu-item-name {
  padding: 5px;
}

#menu {
  width: 100%;
  max-width: 1400px;
  margin: 50px 0;
  padding: 0 50px;
}

#cart-summary {
  position: relative;
  margin-bottom: 50px;
  width: 450px;
  min-width: 300px;
  height: 100%;
  border: 1px solid #cfcece;
}

#cart-summary > h2 {
  font-size: 1.3rem;
  margin-bottom: 10px;
  padding: 10px;
  background: #e6e6e6;
}

#cart-summary > div {
  font-size: 1.15rem;
  padding: 10px 10px;
  margin-bottom: 10px;
  justify-content: space-between;
}

.item-breakdown {
  font-size: 0.9rem;
  text-align: left;
  justify-content: space-between;
}

.item-breakdown > div {
  width: 30px;
}

.item-breakdown div:nth-child(2) {
  width: 150px;
}

.item-breakdown div:nth-child(3) {
  width: 50px;
}

.checkout {
  bottom: 0;
  height: 40px;
  width: 100%;
  font-size: 1.3rem;
  font-weight: bold;
  border: none;
  color: #fff;
  text-shadow: 0 0 0.1rem #000;
  background: #db9768;
}

.checkout:hover {
  cursor: pointer;
  background: #bd7038;
}

/* --------------------------- */
/* ------ SMALL DISPLAY ------ */
/* --------------------------- */

#cart-summary-sm {
  position: relative;
  margin-bottom: 50px;
  width: 60%;
  min-width: 370px;
  height: 100%;
  border: 1px solid #cfcece;
}

#cart-summary-sm > h2 {
  margin-bottom: 10px;
  padding: 10px;
  background: #e6e6e6;
}

#cart-summary-sm > div {
  font-size: 1.25rem;
  padding: 10px 10px;
  margin-bottom: 10px;
  justify-content: space-between;
}

.cart-item {
  position: relative;
  margin-left: 0;
  margin-bottom: 10px;
  width: 100%;
  max-width: 700px;
}

.cart-item-image-container {
  width: 80px;
  min-width: 80px;
  height: 110px;
  min-height: 110px;
}

.cart-item-image-container > img {
  width: 100%;
  height: 100%;
}

.cart-item-info {
  margin-left: 5px;
  padding: 10px;
  width: 100%;
  justify-content: space-between;
  align-items: flex-start;
  background: #f3f3f3;
  font-size: 1.05rem;
}

.cart-item-quantity > input {
  border: 1px solid #9c9c9c;
  color: rgb(114, 114, 114);
  width: 30px;
  text-align: center;
  outline: none;
}

.cart-item-btn {
  cursor: pointer;
  position: absolute;
  bottom: 12px;
  width: 30px;
  height: 30px;
  justify-content: center;
  align-items: center;
  background: #fff;
  border: 1px solid #cfcece;
}

.cart-item-btn:hover {
  background: #cfcece;
}

.decrement {
  right: 12px;
}

.increment {
  right: 54px;
}

/* --------------------------- */
/* ------ SMALL DISPLAY ------ */
/* --------------------------- */

.cart-item-sm {
  position: relative;
  margin-bottom: 10px;
  width: 60%;
  min-width: 370px;
  text-align: left;
}

#cart {
  width: 100%;
  max-width: 1400px;
  margin: 50px;
  padding: 0 50px;
  justify-content: center;
}

#cart-display {
  width: 100%;
  margin-left: 50px;
}

#empty-cart {
  width: 100%;
  height: 100%;
  font-size: 1.5rem;
  justify-content: center;
  text-align: center;
}

/* --------------------------- */
/* ------ SMALL DISPLAY ------ */
/* --------------------------- */

#cart-display-sm {
  width: 100%;
  margin-bottom: 50px;
  text-align: center;
  justify-content: center;
  align-items: center;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

.flex {
  display: flex;
}

.flex-col {
  display: flex;
  flex-direction: column;
}

.align-center {
  align-items: center;
}

.center-items {
  justify-content: center;
  align-items: center;
}

.margin-bot-25 {
  margin-bottom: 25px;
}

.margin-bot-50 {
  margin-bottom: 50px;
}

.margin-bot-100 {
  margin-bottom: 100px;
}

.font-brown {
  color: #db9768;
}

.cpm-text {
  font-family: Cuprum, sans-serif;
}

.comm-text {
  font-family: Commissioner, monospace;
}

.app-wrapper {
  display: flex;
  flex-direction: column;
  align-items: center;
}

